  display: flex !important;
}
.form-popup {
  display: flex;
  top: 0 !important;
  text-align: left;
  justify-content: space-between;
  flex-direction: column;
  overflow-y: scroll;
}
#form-main {
  padding: 5px 0 0 5px;
